.back{
    display: flex;
    flex-direction: row;
    align-items: start;
    margin: 40px 30px 40px 30px;
    padding: 1px 20px 1px 20px;
    border-radius: 15px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}

.dialog_users{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start; 
    margin-top: 10px;
}

.nav_user{
    display: flex;
    flex-direction: row;
    line-height: 1;
    text-decoration:none;
    cursor: pointer;
    position:relative;
    align-items: center;
    width: 300px;
}

.nav_user.active{
    color: #f38181;
  }

.user_name{
    margin: 7px 20px 7px 10px;
    font-size: 24px;
    color: #95e1d3;
}

.user_photo{
    width: 25px;
    height: 25px;
    border-radius: 30px;
}

.messages{
    margin-left: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: start;
    justify-items: space-between;
    width: 100%;
}

.reciever{
    margin-right: auto;
}

.reciever .user_message{
    min-width: 160px;
    display: flex;
    justify-content: end;
    width: fit-content;
    margin: 6px 0 50px 0;
    padding: 8px 20px 8px 20px;
    border-radius: 10px 10px 10px 0px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}

.sender .user_message{
    min-width: 160px;
    display: flex;
    justify-content: start;
    width: fit-content;
    margin: 50px 0 6px 0;
    padding: 8px 20px 8px 20px;
    border-radius: 10px 10px 0px 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}

.send_message{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between; 
    width: 100%;
}

.send_input{
    width: 100%;
    margin: 20px 3px 15px 0;
}

.send_btn{
    margin: 20px 0 15px 3px;
}

.active{
    color: #f38181;
}